import { cn } from "@/lib/utils";
import { StepperIcon, StepperOtherIcon } from "@/assets/icons";
import Link from "next/link";

interface StepperProps {
  steps: string[];
  activeStep: number;
}

export const Stepper = ({ steps, activeStep }: StepperProps) => {
  const generateStepPath = (index: number) => {
    const basePath = "/cart";
    if (index === 0) return basePath;
    return `${basePath}/${steps[index].toLowerCase()}`;
  };

  return (
    <div className="grid grid-cols-4 gap-5">
      {steps.map((step, index) => {
        const isCompleted = index < activeStep;
        const stepPath = generateStepPath(index);

        return (
          <div key={index} className="relative flex flex-col items-center w-full">
            <div className="relative flex items-center justify-center">
              {index === 0 ? (
                <StepperIcon
                  className={cn(
                    activeStep >= index ? "text-cerulean" : "text-gray-400"
                  )}
                />
              ) : (
                <StepperOtherIcon
                  className={cn(
                    activeStep >= index ? "text-cerulean" : "text-gray-400"
                  )}
                />
              )}
              <div className="absolute">
                {isCompleted ? (
                  <Link
                    href={stepPath}
                    className={cn(
                      "text-sm font-medium hover:text-cerulean/80",
                      activeStep >= index ? "text-cerulean" : "text-gray-400"
                    )}
                  >
                    {step}
                  </Link>
                ) : (
                  <p
                    className={cn(
                      "text-sm font-medium",
                      activeStep >= index ? "text-cerulean" : "text-gray-400"
                    )}
                  >
                    {step}
                  </p>
                )}
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
};